package com.ucasoft.komm.website

import com.ucasoft.wrappers.lucide.Code
import com.ucasoft.wrappers.lucide.Puzzle
import com.ucasoft.wrappers.lucide.Rocket
import com.ucasoft.wrappers.lucide.Tag
import js.objects.unsafeJso
import mui.material.Box
import mui.material.CssBaseline
import mui.material.styles.ThemeProvider
import mui.material.styles.createTheme
import react.FC
import react.create
import react.useRef
import web.cssom.blur
import web.cssom.px
import web.cssom.rem
import web.cssom.rgb
import web.html.HTMLDivElement

val appTheme = createTheme(
    unsafeJso {
        palette = unsafeJso {
            primary = unsafeJso {
                main = rgb(102, 126, 234)
                light = rgb(132, 151, 238)
            }
            secondary = unsafeJso {
                main = rgb(118, 75, 162)
            }
            background = unsafeJso {
                default = "#f8fafc"
                paper = "#ffffff"
            }
            text = unsafeJso {
                primary = rgb(55, 65, 81)
                secondary = rgb(107, 114, 128)
            }
        }
        typography = unsafeJso {
            fontFamily = arrayOf("Inter", "sans-serif").joinToString(",")
            h1 = unsafeJso {
                fontWeight = 700
                fontSize = 2.5.rem
                `@media (min-width:600px)` = unsafeJso {
                    fontSize = 3.rem
                }
                `@media (min-width:900px)` = unsafeJso {
                    fontSize = 3.5.rem
                }
            }
            h2 = unsafeJso {
                fontWeight = 700
                fontSize = 2.rem
                `@media (min-width:600px)` = unsafeJso {
                    fontSize = 2.5.rem
                }
            }
            h3 = unsafeJso {
                fontWeight = 700
                fontSize = 1.75.rem
                `@media (min-width:600px)` = unsafeJso {
                    fontSize = 2.rem
                }
            }
            h4 = unsafeJso {
                fontWeight = 600
                fontSize = 1.5.rem
                `@media (min-width:600px)` = unsafeJso {
                    fontSize = 1.75.rem
                }
            }
            h5 = unsafeJso {
                fontWeight = 600
                fontSize = 1.25.rem
            }
            h6 = unsafeJso {
                fontWeight = 600
                fontSize = 1.1.rem
            }
        }
        components = unsafeJso {
            MuiButton = unsafeJso {
                styleOverrides = unsafeJso {
                    root = unsafeJso {
                        borderRadius = 8.px
                        textTransform = "none"
                    }
                }
            }
            MuiCard = unsafeJso {
                styleOverrides = unsafeJso {
                    root = unsafeJso {
                        borderRadius = 12.px
                        transition = "box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out"
                        `&:hover` = unsafeJso {
                            boxShadow = "0px 10px 20px rgba(0, 0, 0, 0.1)"
                            transform = "translateY(-4px)"
                        }
                    }
                }
            }
            MuiPaper = unsafeJso {
                styleOverrides = unsafeJso {
                    root = unsafeJso {
                        borderRadius = 12.px
                    }
                }
            }
            MuiAppBar = unsafeJso {
                styleOverrides = unsafeJso {
                    root = unsafeJso {
                        backgroundColor = rgb(255, 255, 255, 0.85)
                        backdropFilter = blur(10.px)
                        boxShadow = 1
                    }
                }
            }
        }
    }
)

val App = FC {

    val featuresRef = useRef<HTMLDivElement>(null)
    val targetsRef = useRef<HTMLDivElement>(null)
    val pluginsRef = useRef<HTMLDivElement>(null)
    val installationRef = useRef<HTMLDivElement>(null)

    ThemeProvider {
        theme = appTheme
        CssBaseline {}
        NavBar {
            menu = listOf(
                NavBarMenu(Rocket.create(), "Features", featuresRef),
                NavBarMenu(Tag.create(), "Targets", targetsRef),
                NavBarMenu(Puzzle.create(), "Plugins", pluginsRef),
                NavBarMenu(Code.create(), "Installation", installationRef),
            )
        }
        Box {
            asDynamic().component = "main"
            Hero {}
            Features {
                ref = featuresRef
            }
            Targets {
                ref = targetsRef
            }
            Plugins {
                ref = pluginsRef
            }
            Installation {
                ref = installationRef
            }
            //CodeExamples {}
            Footer {}
        }
    }
}